.catalog-content{
  margin-top: 2.31em;
}

.catalog-slider{
  margin-top: 2.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
      margin-bottom: 2.8125em;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 6);
      padding-right: 1.25em;
      height: auto;
      opacity: 0;
      pointer-events: unset;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
        pointer-events: unset;
      }
      @media screen and (min-width: 993px){
        &-next+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}


.catalog-card{
  box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  padding: 1.63em 1em 1.81em;
  border-bottom: .1875em solid $primary;
  height: 100%;
  position: relative;
  @media screen and (min-width: 993px){
    &:not(:hover){
      .catalog-card__btn{
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  &__btn{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 2.8125em;
    margin-top: -.1875em;
    padding: 0 1em;
    justify-content: unset;
    img{
      right: .625em;
    }
  }
  &__img{
    @include imgCover(93.2%);
    margin-bottom: 3em;
  }
  &__desc{
    font-size: .9375em;
    line-height: 127%;
    margin-top: .67em;
  }
}


@media screen and (max-width: 992px){
  .catalog-content{
    margin-top: 2em;
  }

  .catalog-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        margin-bottom: 4em;
      }
      &-slide{
        width: calc(100% / 3);
        @media screen and (min-width: 577px){
          &-next+.swiper-slide{
            opacity: 1;
            pointer-events: unset;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-slider{
    margin-top: 1em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
        margin-bottom: 3em;
      }
      &-slide{
        width: 50%;
        padding-right: 1em;
      }
    }
  }

  .catalog-card{
    font-size: .75em;
  }
}
