.catalog-modal{
  font-weight: 400;
  .modal{
    &__bg{
      background: rgba(40, 40, 40, 0.80);
      backdrop-filter: blur(7.5px);
    }
    &__dialog{
      width: 90.625em;
      background-color: #fff;
      border-bottom: .625em solid $primary;
    }
    &__close{
      bottom: unset;
      left: unset;
      margin: 0;
      top: 1.31em;
      right: 1.62em;
      path{
        fill: $black;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
  }
  .lines{
    position: absolute;
    height: 100%;
    padding: 0 20em;
  }
  &__top{
    padding: 2.625em 3.25em;
  }
}

.product-info{
  margin-top: 2.31em;
  display: grid;
  grid-template-columns: 40.5em calc(100% - 43em);
  gap: 1.5em 2.5em;
  &__desc{
    font-size: 1.125em;
    line-height: 128%;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    padding: 0 1.5em;
    height: 2.8125em;
    .product-info__btn-num{
      font-size: 1.5em;
      padding-right: .3em;
    }
    .product-info__btn-title{
      padding-top: .125em;
    }
  }
  &__title{
    font-size: 1.375em;
    line-height: 128%;
    color: #434343;
    font-weight: 700;
  }
  &__scheme{
    display: block;
    width: 100%;
    max-width: 33.25em;
    margin-top: 1.875em;
  }
  .m-list{
    margin-top: 1.875em;
  }
}

.text18{
  font-size: 1.125em;
  line-height: 127%;
}

.product-count{
  margin-top: 3.13em;
  display: flex;
  align-items: center;
  &__title{
    color: #898989;
  }
  .fg{
    margin: 0 .56em 0 .75em;
    width: 7.5em;
    input{
      height: 2.44em;
      padding: 0 .67em;
      border: 1px solid #D9D9D9;
      background: #F8F8F8;
    }
  }
}

.product-info2{
  margin-top: 5.5em;
}

.construction{
  &__wrap{
    display: flex;
    margin-top: 1.75em;
  }
  &__img{
    width: 9.25em;
    height: 19.875em;
    display: flex;
    justify-content: center;
    margin-right: 2em;
    flex-shrink: 0;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__items{
    max-width: 14em;
  }
  &__item{
    color: #777;
    font-size: 1em;
    line-height: 125%;
    margin-bottom: 1.56em;
  }
}

.m-list{
  @include grid100gap(1em);
  li{
    font-size: 1.125em;
    line-height: 127%;
    position: relative;
    padding-left: 1.78em;
    &:before{
      content: '';
      position: absolute;
      top: .45em;
      left: 0;
      width: .38em;
      height: .38em;
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-modal{
    &__top{
      padding: 4.5em 1em 1.5em;
    }
  }

  .product-info{
    grid-template-columns: 100%;
    &__desc{
      font-size: 1em;
    }
    &__title{
      font-size: 1.125em;
    }
  }

  .product-count{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .m-list{
    font-size: .75em;
  }

  .construction{
    &__img{
      font-size: .75em;
    }
    &__item{
      font-size: .75em;
    }
  }
}
