.modal,.search-modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(#F8F8F8, 0.9);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .search-modal__bg,
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 4em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }
    &__dialog {
        background: #fff;
        width: 36.375em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        display: inline-block;
    }
    &__close{
        position: absolute;
        width: 3em;
        bottom: 100%;
        left: 100%;
        margin-left: 1.12em;
        margin-bottom: .31em;
        background-color: transparent;
        @include center;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary
            }
        }
    }
}

.conf-modal{
    .modal{
        &__bg{
            background: rgba(40, 40, 40, 0.80);
            backdrop-filter: blur(7.5px);
        }
        &__dialog{
            width: 100em;
            background-color: #fff;
            border-top: .625em solid $primary;
        }
    }
    &__top{
        padding: 3.06em;
        @include center;
        text-align: center;
        text-transform: uppercase;
        font-weight: 700;
        border-bottom: 1px solid #CED6E1;
    }
    &__bottom{
        padding: 1.875em 5em 4em;
    }
}

.text{
    font-weight: 400;
    b{
        font-weight: 900;
    }
    h3{
        font-size: 1.25em;
        line-height: 125%;
        font-weight: 700;
        margin-bottom: 1em;
    }
    p{
        font-size: 1.125em;
        line-height: 125%;
        @include lastM(0 0 1.12em);
    }
    hr{
        height: 1em;
        opacity: 0;
        display: block;
    }
}


@media screen and (max-width: 992px) {
    .modal {
        //padding: 3em 0;
        &__close {
            margin-bottom: .25em;
            right: 1.25em;
            left: unset;
            bottom: unset;
            top: 1.25em;
            width: 2.25em;
            path{
                fill: #fff;
            }
        }

        &__top {
            gap: .75em;
        }
        .help-form{
            padding-top: 3.69em;
        }
    }

    .conf-modal{
        .modal{
            &__dialog{
                border-width: .3125em;
            }
            &__close{
                top: unset;
                bottom: 100%;
                margin-bottom: 1em;
                right: 0;
            }
        }
        &__top{
            padding: 1.06em .875em .625em;
        }
        &__bottom{
            padding: 1em 1.25em 2em;
        }
    }

    .text{
        h3{
            font-size: 1em;
            margin-bottom: .625em;
        }
        p{
            font-size: .9375em;
        }
    }
}

@media screen and (max-width: 576px) {
    .modal {
        &:after {
            display: none;
        }

        &__dialog {
            margin: 0 auto;
        }
    }


}
