.team,
.events,
.help,
.service,
.solutions,
.objects{
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  .lines{
    position: absolute;
    height: 100%;
    z-index: unset;
  }
}

.service{
  padding: 3.38em 0 3.88em;
  background: #F5F5F5;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.25em));
    justify-content: space-between;
    margin-top: 7.57em;
    .service-item{
      @media screen and (min-width: 993px){
        &:nth-child(4n - 3){
          transform: translateY(-4.62em);
        }
        &:nth-child(4n - 2){
          transform: translateY(-3.82em);
        }
        &:nth-child(4n - 1){
          transform: translateY(-1.63em);
        }
      }
    }
  }
}

.service-item{
  &__icon{
    img{
      max-width: 6.875em;
      max-height: 6.875em;
      display: block;
      transition: all .4s;
      &:hover{
        transform: translateY(-1em);
      }
    }
  }
  &__title{
    max-width: 16.375em;
    margin-top: 1.25em;
    font-weight: 400;
  }
}

.roofs{
  padding: 5.85em 0 4.7em;
  position: relative;
  &__img{
    position: absolute;
    bottom: 0;
    left: 6.69em;
    width: 23.875em;
  }
  &__grid{
    position: relative;
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 30.375em;
    max-width: 100%;
  }
}

.roofs-slider{
  @media screen and (min-width: 993px){
    width: 75%;
    margin-left: calc(25% - .8125em);
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3));
        gap: 3.5em 0;
        transform: unset !important;
        &:before{
          content: '';
        }
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.roof-card{
  max-width: 22.875em;
  &._no-shadow{
    img{
      filter: unset;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.625em;
      height: 1.625em;
      background-image: url(../img/roof-icon.svg);
      @include bgImgSize(100%);
      margin-right: 1.25em;
    }
  }
  &__img{
    margin-top: 1em;
    padding-left: 2.875em;
    img{
      filter: drop-shadow(.9375em 1.875em 1.25em rgba(0, 0, 0, 0.15));
      max-width: 100%;
      max-height: 11em;
    }
  }
}

.about{
  padding: 3.56em 0 5.88em;
  position: relative;
  &__img{
    position: absolute;
    right: 14.69em;
    top: 7.56em;
    z-index: -1;
    width: 34.9375em;
  }
  &__video{
    margin-top: 2.62em;
    margin-left: 11.38em;
    width: 46.375em;
    max-width: 100%;
  }
}

.video{
  position: relative;
  display: block;
  border-bottom: .3125em solid $primary;
  border-right: .3125em solid $primary;
  &__img{
    width: 100%;
    display: block;
  }
}

.play-btn{
  @include centerAbsolute;
  @include center;
  width: 6.375em;
  height: 6.375em;
  background-color: #fff;
  border-radius: 50%;
  &:hover{
    background-color: $primary;
    path{
      fill: #fff;
    }
  }
  svg{
    width: 1.5em;
    height: auto;
  }
}

.solutions{
  padding: 3.75em 0 4.38em;
  background: #F3F3F3;
  overflow: hidden;
  &__img{
    width: 22.2em;
    position: absolute;
    right: 0;
    top: 33.12em;
  }
  &__top{
    max-width: 35em;
  }
  &__items{
    @include grid100gap(3.125em);
  }
}

.solution-item{
  display: grid;
  grid-template-columns: calc(100% - 42.75em) 42.75em;
  align-items: flex-end;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: 1.94em 2.25em 12em;
    background-color: #fff;
    min-height: 38.875em;
    position: relative;
  }
  &__info{
    padding: .69em 2.37em .69em 1.12em;
    border-radius: 0.625em;
    border: 1px dashed #BABABA;
    margin-top: .56em;
    display: inline-flex;
    align-items: center;
    &-content{
      text-transform: uppercase;
      margin-right: 2.37em;
      .text14{
        color: #8D8D8D;
      }
    }
    .text40{
      font-family: $font2;
    }
  }
  &__btn{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 9.75em;
  }
  @media screen and (min-width: 993px){
    &:nth-child(even){
      grid-template-columns: 42.75em calc(100% - 42.75em);
      .solution-item__img{
        order: -1;
      }
    }
  }
}

.solution-advantages{
  margin-top: 2.69em;
  &__grid{
    display: grid;
    grid-template-columns: .9fr 1fr;
    justify-content: space-between;
    gap: 2.3em;
  }
  ._type2{
    .solution-advantages__items{
      li:before{
        background-image: url(../img/minus.svg);
      }
    }
  }
  &__items{
    margin-top: 1.56em;
    @include grid100gap(1.12em);
    li{
      padding-left: 2.5em;
      position: relative;
      &:before{
        content: '';
        width: 1.5em;
        height: 1.5em;
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(../img/plus.svg);
        @include bgImgSize(100%);
      }
      span{
        font-size: 1.125em;
        line-height: 125%;
      }
    }
  }
}

.set{
  padding: 4.35em 0;
  position: relative;
  &__img{
    position: absolute;
    z-index: -1;
    width: 28.1875em;
    right: 0;
    top: 7.38em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
    margin-top: 11.44em;
  }
  @media screen and (min-width: 993px){
    .set-card{
      &:nth-child(4n-3){
        transform: translateY(-7.56em);
      }
      &:nth-child(4n-2){
        transform: translateY(-5.06em);
      }
      &:nth-child(4n-1){
        transform: translateY(-2.56em);
      }
    }
  }
}

.set-card{
  position: relative;
  &__img{
    @include imgCover(100%);
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    padding: 2.5em 2em;
    color: #fff;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 52.5%, #000 100%);
  }
}

.projects{
  padding: 5.19em 0 4.12em;
  &__top{
    max-width: 52em;
    .text30{
      text-transform: uppercase;
    }
  }
  &__text{
    text-transform: uppercase;
    .text30,
    .text48{
      display: inline;
    }
  }
}

.projects-slider2{
  margin-top: 2.56em;
  @media screen and (min-width: 993px){
    .project-card{
      width: 22.8125em;
      position: absolute;
      &:nth-child(1){
        top: 11.5em;
        left: 0;
        height: 24.3125em;
      }
      &:nth-child(2){
        top: 0;
        left: 22.8125em;
        height: 29.8125em;
      }
      &:nth-child(3){
        top: 2.7em;
        left: calc(22.8125em * 2);
        width: 45.3125em;
        height: 20.8125em;
      }
      &:nth-child(4){
        top: 29.8125em;
        left: 22.8125em;
        height: 17.75em;
      }
      &:nth-child(5){
        top: calc(20.8125em + 2.7em);
        left: calc(22.8125em * 2);
        width: 22.8125em;
        height: 20.8125em;
      }
      &:nth-child(6){
        top: calc(20.8125em + 2.7em);
        left: calc(22.8125em * 3);
        width: 26.8125em;
        height: 17.125em;
      }
    }
    .swiper{
      &-wrapper{

      }
      &-slide{
        position: relative;
        transform: unset !important;
        height: 47.5em;
        opacity: 0;
        transition: all .3s;
        pointer-events: none;
        &-active{
          opacity: 1;
          pointer-events: unset;
        }
        .project-card{
          &__img{
            height: 100%;
            padding: 0;
          }
        }
      }
    }
  }
}

.projects-slider{
  margin-top: 2.56em;
  display: none;
}

.project-card{
  position: relative;
  color: #fff;
  overflow: hidden;
  display: block;
  &:hover{
    color: #fff;
    .project-card__img img{
      transform: scale(1.05);
    }
    .project-card__content:before{
      opacity: 1;
    }
  }
  &__img{
    @include imgCover(106.5%);
    img{
      transition: all .5s;
    }

  }
  &__content{
    position: absolute;
    padding: 1.88em 1.75em;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.80) 0%, rgba(0, 0, 0, 0.00) 39.06%), linear-gradient(180deg, rgba(0, 0, 0, 0.00) 69.18%, rgba(0, 0, 0, 0.80) 100%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:before{
      content: '';
      position: absolute;
      background-color: rgba(0, 0, 0, 0.30);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all .3s;
      opacity: 0;
    }
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__title{
    font-family: $font2;
  }
}

.objects{
  background: #F5F5F5;
  padding: 3.25em 0;
  &__img{
    margin: -13.7em auto 0;
    max-width: 100%;
    display: block;
    width: 76.875em;
    &._view2{
      display: none;
    }
  }
  &__top{
    position: relative;
    z-index: 2;
  }
  &__num{
    margin-top: -1em;
    padding-left: 26.88em;
  }
  &__subtitle{
    margin-top: -1.125em;
    padding-left: 26.88em;
    font-weight: 300;
    text-transform: uppercase;
  }
}

.partner{
  padding: 1.5em 0 5.87em;
  .container{
    padding-right: 0;
  }
  &__inner{
    display: flex;
    align-items: flex-end;
  }
  &__left{
    display: flex;
    align-items: flex-start;
    margin-left: -1.25em;
    flex-shrink: 0;
  }
  &__check{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: .875em;
  }
  &__logo{
    width: 26.5625em;
    margin-top: .875em;
  }
  &__right{
    flex-grow: 1;
    border-bottom: .625em solid $primary;
    padding-bottom: .75em;
    margin-left: 3.69em;
    margin-bottom: .7em;
  }
}

.companies{
  padding: 4.35em 0 1.5em;
  overflow: hidden;
  &__btn{
    margin: 0 -14.5em 0 auto;
    width: 48.25em;
    height: 9.75em;
    img{
      right: 1.75em;
      width: 6.25em;
    }
  }
}

.companies-slider{
  margin-top: 4.62em;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 25%;
      height: auto;
    }
  }
}

.company-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  &__img{
    height: 3.875em;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: 0 1em;
    margin-bottom: 1.69em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .service{
    padding: 1.94em 0 2.25em;
    &__items{
      margin-top: .88em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .service-item{
    display: flex;
    align-items: center;
    &__icon{
      width: 4.375em;
      flex-shrink: 0;
      margin-right: 1.56em;
      img{
        max-width: 100%;
        max-height: 4.375em;
      }
    }
    &__title{
      margin: 0;
    }
  }

  .roofs{
    overflow: hidden;
    padding: 2.35em 0 1.9em;
    &__top{
      position: static;
    }
    &__img{
      display: none;
    }
  }

  .roofs-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width: 15em;
        padding-right: 2em;
      }
    }
  }

  .roof-card{
    &__img{
      padding-left: 2.5em;
      img{
        max-width: 7.375em;
      }
    }
  }

  .about{
    padding: 1.9em 0 5.06em;
    &__img{
      left: -1.88em;
      right: unset;
      bottom: .81em;
      top: unset;
      width: 13.6875em;
    }
    &__video{
      width: 100%;
      margin-left: 0;
      margin-top: 1.56em;
    }
  }

  .play-btn{
    width: 3.75em;
    height: 3.75em;
    svg{
      width: 1.125em;
    }
  }

  .solutions{
    padding: .81em 0 1.875em;
    &__img{
      display: none;
    }
    &__top{
      max-width: 19em;
    }
    &__items{
      margin-top: 1.19em;
      gap: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .solution-item{
    height: 100%;
    display: flex;
    flex-direction: column;
    &__img{
      order: -1;
    }
    &__content{
      flex-grow: 1;
      min-height: unset;
      padding: .94em .94em 5.5em;
    }
    &__btn{
      height: 4.5625em;
      padding-left: 2.56em;
      padding-right: 0;
      justify-content: unset;
      span{
        font-size: 1em;
      }
    }
    &__info{
      display: block;
      padding: .69em 1.13em .38em;
    }
  }

  .solution-advantages{
    margin-top: .94em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__items{
      margin-top: .625em;
      gap: .625em;
      li{
        span{
          font-size: 1em;
        }
      }
    }
  }

  .set{
    padding: 1.875em 0 1em;
    &__img{
      display: none;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
      margin-top: 1.5em;
    }
  }

  .set-card{
    &__content{
      padding: 1.19em .75em 1.19em 1.5em;
    }
    &__img{
      padding-bottom: 68.75%;
    }
  }

  .projects{
    padding: 2.44em 0 1.25em;
    &__top{
      @include grid100gap(.625em);
    }
  }

  .projects-slider{
    margin-top: 1.875em;
    overflow: hidden;
    display: block;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 50%;
        padding-right: 1em;
      }
      &-nav{
        margin-top: 1.81em;
      }
      &-button{
        display: flex;
      }
      &-pagination{
        margin: 0 1.5em;
      }
    }
  }

  .projects-slider2{
    display: none;
  }

  .project-card{
    &__content{
      padding-bottom: 1.44em;
    }
  }

  .objects{
    padding: 1.875em 0 1.625em;
    .title{
      max-width: 10em;
    }
    &__num,
    &__subtitle{
      padding-left: 3em;
      margin-top: -.25em;
    }
    &__num{

    }
  }

  .partner{
    padding: 1.2em 0 1.875em;
    &__inner{
      display: block;
    }
    &__left{
      margin: 0;
      align-items: center;
    }
    &__check{
      margin-right: 1.31em;
    }
    &__logo{
      margin: 0;
      width: 12.3125em;
    }
    &__right{
      margin: 1.12em 0 0;
      padding-bottom: .56em;
      border-width: .375em;
      .text18{
        margin-top: .31em;
      }
    }
  }

  .companies{
    padding: 1em 0 1.2em;
    &__btn{
      margin: 1.875em -1.25em 0 0;
      width: calc(100% + 1.25em);
      height: 5.625rem;
      padding: 0 0 0 3em;
      justify-content: unset;
      img{
        width: 3.75em;
        right: .875em;
      }
      span{
        font-size: 1em;
      }
    }
  }

  .companies-slider{
    margin-top: 1.81em;
    .swiper{
      &-slide{
        width: 12.5em;
      }
      &-nav{
        margin-top: .75em;
      }
    }
  }

  .company-card{
    justify-content: unset;
    &__img{
      height: 2.75em;
      margin-bottom: 1em;
      padding: 0 1em;
    }
  }
}


@media screen and (max-width: 576px) {
  .service {
    .title{
      max-width: 90%;
      br{
        display: none;
      }
    }
    &__items {
      grid-template-columns: 100%;
    }
  }

  .solutions{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .set{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .projects-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .objects{
    &__img{
      max-width: unset;
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      margin-top: -8em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
}
