.req{
  @media screen and (min-width: 993px){
    padding-top: 0;
    padding-bottom: 0;
    &__inner{
      padding-top: 3.25em;
      padding-bottom: 1.5em;
    }
  }
}
.projects{
  overflow: hidden;
}
.help{
  padding-top: 0;
}
.roofs__img{
  height: auto;
}

._fill{
  transition: all .9s;
  opacity: 1 !important;
  transform: unset !important;
}

.reviews-slider{
  overflow: unset;
  .swiper{
    &-slide{
      opacity: 0;
      pointer-events: unset;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
        pointer-events: unset;
      }
      @media screen and (min-width: 993px){
        &-next+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.review-card{
  &__img{
    img{
      box-shadow: 10px 10px 40px rgba(0, 0, 0, 0.25);
      transform: translate(-50%,-50%);
      left: 50%;
      top: 50%;
      width: calc(100% - 2em);
      height: calc(100% - 2em);
      transition: all .3s;
    }
  }
  &:hover{
    .review-card__img img{
      box-shadow: 0 0 2.875em rgba(0,0,0,.3);
    }
  }
}

.main-economy{
  &__nav{
    display: flex;
    margin-top: 3em;
    padding-bottom: .81em;
  }
  &__link{
    display: flex;
    align-items: center;
    span{
      line-height: 125%;
      text-decoration-line: underline;
    }
    img{
      width: 1.4375em;
      margin-left: 2.5em;
    }
    &:hover{
      color: #fff;
      opacity: .8;
    }
  }
}

.event-card__bottom{
  padding-left: 0;
  padding-right: 0;
}

._number-animation{
  transition: all .3s;
}

.advantage-item{
  .odometer.odometer-auto-theme, .odometer.odometer-theme-default{
    font-family: inherit;
  }
}

.advantage-item__top{
  .text150{
    height: .9375em;
    overflow: hidden;
    margin-bottom: .0625em;
  }
}


@media screen and (min-width: 993px){
  .advantage-item--pt{
    padding-top: 2.75em;
  }

  .main__bottom{
    margin-top: -10.625em;
  }
}
