.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: 1px solid #E2E2E2;
    padding: 0 1.11em;
    height: 3.11em;
    width: 100%;
    font-size: 1.125em;
    background-color: #E2E2E2;
    color: #000;
    &::placeholder {
      color: #272727;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    resize: none;
    padding-top: .8125em;
    padding-bottom: .8125em;
    height: 5.5em;
  }
  &._dark{
    textarea,
    input{
      background-color: transparent;
      border-color: #fff;
      color: #fff;
      &::placeholder{
        color: #fff;
      }
      &:focus {
        border-color: $primary;
      }
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    textarea{
      height: 5em;
    }
  }
}
