@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-orange{
    background-color: $primary;
    height: 4.5625em;
    font-weight: 500;
    color: #fff;
    position: relative;
    span{
        font-size: 1.125em;
    }
    img{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 1.56em;
        width: 1.4375em;
    }
    &:hover{
        color: #fff;
        background-color: darken($primary,20%);
    }
}
