@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper{
  &-nav{
    margin-top: 3.125em;
    @include center;
  }
  &-pagination{
    margin: 0 1em;
    min-height: 1.25em;
    position: static;
    width: unset !important;
    @include center;
    &-bullet{
      position: relative;
      border-radius: 0;
      width: .625em;
      height: .625em;
      opacity: 1;
      background-color: $primary;
      margin: 0 .3125em !important;
      &:before{
        content: '';
        @include centerAbsolute;
        width: 200%;
        height: 200%;
        border: 1px solid $primary;
        transition: inherit;
        opacity: 0;
        pointer-events: none;
      }
      &-active:before{
        opacity: 1;
      }
    }
  }
  &-button{
    position: static;
    width: 5.5em;
    height: auto;
    margin: 0;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}


.animate-path{
  transition: all 10s;
  transform: unset !important;
  &.animated--active{
    stroke-dasharray: 100,100;
  }
}

@media screen and (min-width: 993px){
  .animated{
    transition: transform 1s;
    transform: translateY(3em);
    &--active{
      transform: translateY(0);
    }
  }
}


@media screen and (max-width: 992px){
  .swiper{
    &-nav{
      margin-top: 1.875em;
    }
    &-button{
      display: none;
    }
    &-pagination{
      margin: 0;
    }
  }
}
