.props{
  padding: 2em 3.25em 3em;
  background: #F5F5F5;
  //border-left: .375em solid $primary;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: .375em;
    height: 100%;
    background-color: $primary;
  }
  &__items{
    margin-top: 1.88em;
    display: grid;
    grid-template-columns: 40.5em calc(100% - 43em);
    gap: .625em 2.5em;
  }
  .lines{
    z-index: unset;
    //margin-left: -.625em;
  }
}

.prop-item{
  @include grid100gap(.375em);
  &__title,
  &__desc{
    font-size: 1.125em;
    line-height: 128%;
  }
  &__desc{
    color: #7B7B7B;
  }
}

.temperature{
  padding: 3.38em 3.25em 2.125em;
  .product-info{
    margin-top: 2.625em;
  }
  &__chart{
    width: 34.75em;
    max-width: 100%;
    display: block;
  }
  &__info{
    margin-top: 2.5em;
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    color: #929292;
    strong{
      font-weight: 700;
      color: $black;
    }
  }
}

.temperature-table{
  margin-top: 1.44em;
  width: 25em;
  max-width: 100%;
  table{
    width: 100%;
  }
  th,td{
    font-size: 1.125em;
    line-height: 127%;
    width: 7em;
    &:first-child{
      width: 5em;
    }
  }
  th{
    text-align: left;
    padding: 0 0 1em;
    font-weight: 400;
  }
  td{
    padding: .3125em 0;
  }
  .gray-text{
    color: #7B7B7B;
  }
  span{
    font-size: 1.125em;
  }
}

.certificates{
  padding: 2.125em 3.25em 5em;
  &__items{
    margin-top: 1em;
    display: flex;
    flex-wrap: wrap;
  }
}

.certificate-item{
  width: 12em;
  padding-right: 1em;
  margin-top: 1em;
  &__img{
    height: 4em;
    width: auto;
    margin-bottom: 1.5em;
    display: block;
  }
  &__title{
    font-size: 1em;
    line-height: 1.5625em;
    color: #7B7B7B;
  }
}



@media screen and (max-width: 992px){
  .props{
    padding: 1.5em 1em;
    &:before{
      display: none;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .certificates,
  .temperature{
    padding: 1.5em 1em;
  }
}


@media screen and (max-width: 576px){
  .prop-item {
    &__title,
    &__desc {
      font-size: .75em;
    }
  }

  .temperature{
    &__info{
      font-size: .75em;
    }
  }

  .temperature-table{
    table{
      font-size: .75em;
    }
  }

  .certificate-item{
    width: 50%;
    font-size: .75em;
  }
}
