.contacts-page{
  padding: 3.62em 0 4.37em;
  .contacts__inner{
    margin-top: 3.56em;
  }
  .contacts__info{
    border-left: 5em solid $primary;
    padding-left: 1.56em;
  }
  &__item{
    position: relative;
    display: block;
    &._address{
      margin-bottom: 4.06em;
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-100%,-50%);
    margin-left: -1.4375em;
    width: 5em;
    @include center;
    img{
      display: block;
      max-width: 1.9375em;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    padding: 1.5em 0 0;
    .contacts__info{
      border: none;
      padding: 0 0 1.5em;
      text-align: left;
    }
    &__icon{
      display: none;
    }
    &__item{
      &._address{
        margin-bottom: .5em;
      }
    }
    .contacts__inner{
      margin-top: 2em;
    }
    .contacts__email{
      justify-content: unset;
    }
  }
}
