.main{
  padding: 1.31em 0 5.85em;
  position: relative;
  &__bg{
    position: absolute;
    top: 15em;
    left: 3.5em;
    z-index: -1;
    width: 12.75em;
  }
  .container{
    @media screen and (min-width: 993px){
      padding-right: 0;
    }
  }
  &__top{
    display: grid;
    grid-template-columns: 22.9375em calc(100% - 22.9375em - 2.31em);
    justify-content: space-between;
  }
  &__content{
    padding-top: 1.7em;
  }
  &__img{
    padding-right: 1.81em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: -13.925em;
    padding-left: 20.75em;
    position: relative;
    z-index: 2;
  }
}

.main__nav,
.advantages{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
  gap: .625em;
}

.main-economy{
  margin-top: .875em;
  max-width: 20.75em;
  padding: 1.19em 2em 1.19em 1.19em;
  background-color: $primary;
  color: #fff;
  border-radius: 0.9375em 0 0 0;
  &__percent{
    display: flex;
    font-family: $font2;
    font-weight: 700;
    height: 8em;
  }
  &__measure{
    font-size: 5em;
    margin-left: .025em;
  }
}

.advantages{
  margin-top: .625em;
}

.advantage-item{
  border-radius: 0 0 0.9375em 0.9375em;
  background: #EAEAEA;
  padding: 1.94em 2.375em;
  position: relative;
  transition: all .3s;
  overflow: hidden;
  &:hover{
    box-shadow:0 0 2.875em rgba($primary,.3);
    .advantage-item__img{
      transform: translateY(-50%) scale(1.05);
    }
  }
  &._black{
    background: $black;
    color: #fff;
  }
  &._orange{
    background: $primary;
    color: #fff;
  }
  &__top,&__desc{
    position: relative;
    z-index: 2;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    font-family: $font2;
    font-weight: 700;
  }
  &__period{
    margin-left: .25em;
    transform: translateY(-1.25em);
  }
  &__desc{
    margin-top: -1em;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    height: 8.9375em;
    display: block;
    transition: all .3s;
  }
}

.req,
.contacts{
  .container{
    @media screen and (min-width: 993px){
      padding-right: 0;
    }
  }
}

.req{
  background-color: $black;
  color: #FFF;
  padding: 2.75em 0 1.5em;
  position: relative;
  &__inner{
    padding-right: 34.125em;
    display: grid;
    grid-template-columns: 24em 39.875em;
    align-items: flex-start;
  }
  &__text{
    @include grid100gap(.25em);
  }
  &__btn{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 34.125em;
    padding: 0 3em 0 0;
    img{
      width: 6.25em;
    }
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__hint{
    padding-top: .69em;
    font-weight: 400;
    button{
      color: #fff;
      font-weight: 500;
      &:hover{
        color: $primary;
      }
    }
  }
}

.contacts{
  padding-top: 8.44em;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 19.625em;
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 59.375em) 59.375em;
  }
  &__info{
    padding: 2.5em 2.5em 2.5em 0;
  }
  &__email{
    margin-top: 3.31em;
    .email-link{
      text-decoration: underline;
    }
  }
}

.contact-item{
  margin-bottom: 2.69em;
  .text24,
  .text18{
    color: #272727;
    line-height: 100%;
  }
  .text24{
    margin-bottom: .8em;
  }
  .text18{
    margin-bottom: .33em;
  }
}

.map{
  height: 28.125em;
  width: 100%;
}

.gift{
  padding: 3.5em 0 4.8em;
  &__inner{
    width: 76.875em;
    max-width: 100%;
    margin-left: auto;
    padding: 2.13em 3.5em 2.3em 13.7em;
    border-right: .5em solid $primary;
    border-bottom: .5em solid $primary;
    background-color: #fff;
    position: relative;
    box-shadow: 0 .25em 2.5em 0 rgba(0, 0, 0, 0.10);
  }
  &__text{
    @include grid100gap(.625em);
  }
  &__title{
    span{
      text-transform: uppercase;
    }
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
    max-width: 39.875em;
    margin-top: 3em;
  }
  &__hint{
    max-width: 36.575em;
    margin-top: 3em;
    color: #616161;
    font-weight: 400;
    button{
      color: #202020;
      font-weight: 500;
      &:hover{
        color: $primary;
      }
    }
  }
  &__btn{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 19.5em;
    height: 6.25em;
    padding: 0 2em 0 0;
    img{
      width: 2.375em;
    }
  }
  &__img{
    left: -14.94em;
    top: -3em;
    width: 27.6875em;
    position: absolute;
  }
}

.title,
.title2{
  text-transform: uppercase;
}

.reviews{
  padding: 4.5em 0 3.5em;
  position: relative;
  &__img{
    position: absolute;
    right: 0;
    top: 3.5em;
    z-index: -1;
    width: 36.875em;
  }
  &__top{
    max-width: 35em;
  }
}

.reviews-slider{
  margin-top: 5.19em;
  overflow: hidden;
  .swiper{
    &-container{
      width: calc(100% + 2.56em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 2.56em;
    }
  }
}

.review-card{
  &__img{
    display: block;
    @include imgCover(133%);
  }
}

.consult{
  position: relative;
  padding: 5.31em 0 4.5em;
  &__img{
    width: 49.1875em;
    top: 5.31em;
    right: 9.75em;
    position: absolute;
    z-index: -1;
  }
  &__top{
    margin-bottom: .56em;
    max-width: 39em;
  }
  .gift{
    &__inner{
      width: 69.125em;
      margin: 6.19em 0 0;
      @media screen and (min-width: 993px){
        padding: 3.87em 2em 2em 4.5em;
      }
    }
    &__fields{
      margin: 0;
    }
  }
}

.consult2{
  padding: 4.25em 0 6.94em;
  .consult__top{
    margin-bottom: 1.06em;
  }
  .gift__inner{
    margin-top: 3.5em;
  }
  .consult__img{
    right: 6.87em;
    top: -2.12em;
    width: 40em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.63em 0 2.35em;
    &__bg{
      display: none;
    }
    &__top{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 0;
    }
    &__img{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      padding: 0;
    }
    &__bottom{
      margin-top: -4.56em;
      padding: 0;
    }
    &__nav{
      grid-template-columns: 100%;
      .m-btn{
        padding: 0 3.19em;
        justify-content: unset;
      }
    }
  }

  .main-economy{
    padding: 2.69em 1.38em .625em;
    position: relative;
    br{
      display: none;
    }
    &__percent{
      height: unset;
      position: absolute;
      right: 2.62em;
      top: .38em;
    }
    &__measure{
      font-size: 2.5em;
    }
  }

  .advantage-item{
    padding: 0 1.12em;
    display: flex;
    align-items: flex-start;
    min-height: 5.5em;
    &__desc{
      margin: 0 0 0 .69em;
      padding-top: 2.5em;
      padding-bottom: .56em;
      .text20{
        line-height: 100%;
      }
    }
    &__img{
      height: 4.375em;
    }
    &__period{
      transform: translateY(-.75em);
    }
    .text150{
      line-height: 125%;
    }
  }

  .req{
    padding: 2.25em 0 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
      padding: 0 0 7.3125em;
    }
    &__text{
      gap: .38em;
    }
    &__btn{
      width: 100%;
      height: 5.3125em;
      padding: 0 3em;
      img{
        width: 3.75em;
      }
    }
  }

  .contacts{
    padding-top: 1em;
    &__img{
      position: static;
      width: 12.5em;
      display: block;
      margin: 0 auto;
      transform: unset;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__info{
      padding: .875em 0 2.3em;
      text-align: center;
    }
    &__email{
      margin-top: 2em;
      display: flex;
      justify-content: center;
    }
  }

  .contact-item{
    margin-bottom: 1.94em;
  }

  .map{
    height: 17.5625em;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
  }

  .gift{
    padding: 4.63em 0 1em;
    &__inner{
      padding: 6.5em 1.25em 0 1.875em;
      border-width: .3125em;
    }
    &__img{
      top: -4.63em;
      left: 50%;
      transform: translateX(-50%);
      width: 9.75em;
    }
    &__fields{
      margin-top: 1.25em;
    }
    &__hint{
      margin-top: .94em;
    }
    &__btn{
      position: relative;
      margin-top: .81em;
      margin-right: -1.25em;
      margin-left: auto;
      width: 17.8125em;
      height: 5.3125em;
      padding: 0 0 0 4.56em;
      justify-content: unset;
    }
  }

  .reviews{
    padding: 2.25em 0 1.875em;
    overflow: hidden;
    &__top{
      max-width: 13.625em;
    }
    &__img{
      display: none;
    }
  }

  .reviews-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        padding-right: 1em;
        width: 50%;
      }
    }
  }

  .consult{
    overflow: hidden;
    padding: 1.875em 0 2.25em;
    &__img{
      right: -8em;
      top: 10.125em;
      width: 22.1875em;
    }
    &__top{
      margin-bottom: .625em;
    }
    .gift__inner{
      margin-top: 4.13em;
      padding-top: 1.875em;
    }
  }

  .consult2{
    padding: 1.25em 0 1.875em;
    .title2{
      max-width: 12.5em;
    }
    .consult__top{
      margin-bottom: .625em;
    }
    .gift__inner{
      margin-top: 5em;
    }
    .consult__img{
      right: -2.44em;
      top: 4.8em;
      width: 15.5625em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    grid-template-columns: 100%;
  }

  .req{
    &__fields{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__hint{
      padding-top: .625em;
    }
  }

  .gift{
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .reviews-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
