.catalog{
  padding: 3.625em 0 4.875em;
  position: relative;
  overflow: hidden;
  min-height: 40em;
}

.filter{
  display: flex;
  margin-top: 1.875em;
}

.filter-item{
  width: 18em;
  margin-right: 1.69em;
  &:last-child{
    margin: 0;
  }
}

.filter-select{
  position: relative;
  &._open{
    .filter-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__label{
    color: #7B7B7B;
    font-size: 1em;
    line-height: 125%;
    display: block;
    margin-bottom: .69em;
  }
  &__show{
    position: relative;
    cursor: pointer;
    input{
      border: 1px solid #C1C1C1;
      width: 100%;
      height: 2.44em;
      padding-left: .67em;
      padding-right: 3em;
      color: #222;
      font-size: 1.125em;
      font-family: $font1;
      cursor: pointer;
    }
  }
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    width: 2.5625em;
    height: 100%;
    img{
      position: static;
      transform: unset;
      width: .875em;
    }
  }
  &__dropdown{
    border: 1px solid #C1C1C1;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .25em;
    background-color: #fff;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    z-index: 3;
  }
  &__options{
    max-height: 20em;
    overflow-y: auto;
  }
  &__option{
    position: relative;
    input {
      position: absolute;
      opacity: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 2;
      cursor: pointer;
      &:checked~label{
        background-color: $primary;
        color: #fff;
      }
    }
    label{
      transition: all .3s;
      display: flex;
      align-items: center;
      padding: 0 .67em;
      font-size: 1.125em;
      height: 2.44em;
      font-weight: 400;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 2em 0;
  }

  .filter{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }

  .filter-item{
    width: 100%;
    margin: 0;
  }
}


@media screen and (max-width: 576px){
  .filter{
    grid-template-columns: 100%;
  }
}
