@keyframes rotation {
  0% {
    transform:rotate(0deg);
  }
  100% {
    transform:rotate(360deg);
  }
}

.header,
.footer{
  background-color: #fff;
  position: relative;
  z-index: 2;
  .container{
    padding-right: 0;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .email-link{
    display: none;
  }
}

.header{
  border-bottom: 1px solid #E4E4E4;
}

.footer{
  padding: .375em 0;
}

.logo{
  flex-shrink: 0;
  display: flex;
  align-items: center;
  img{
    display: block;
  }
  &__icon{
    width: 3.75em;
    margin-right: .81em;
    //animation-name: rotation;
    //animation-duration: 6s;
    //animation-iteration-count: infinite;
    //animation-timing-function: linear;
  }
  &__text{
    width: 11.5em;
  }
}

.call-btn{
  flex-shrink: 0;
  display: flex;
  align-items: center;
  color: $primary;
  padding-left: 1.25em;
  border-left: 1px solid #E4E4E4;
  &:hover{
    color: $black;
    .call-btn__rect{
      background-color: $black;
    }
  }
  &__rect{
    transition: inherit;
    margin-left: .25em;
    background-color: $primary;
    @include center;
    flex-shrink: 0;
    width: 6.065em;
    height: 5.3125em;
    clip-path: polygon( 22.433% 6.756%,22.433% 6.756%,22.814% 5.594%,23.319% 4.515%,23.936% 3.53%,24.656% 2.648%,25.466% 1.876%,26.357% 1.225%,27.318% 0.702%,28.338% 0.318%,29.406% 0.081%,30.511% 0%,113.684% 0%,113.684% 100%,9.149% 100%,9.149% 100%,7.514% 99.825%,5.998% 99.324%,4.625% 98.533%,3.42% 97.488%,2.407% 96.227%,1.61% 94.784%,1.053% 93.197%,0.762% 91.502%,0.759% 89.735%,1.07% 87.933%,22.433% 6.756% );
    img{
      width: 1.875em;
    }
  }
}

.menu{
  flex-grow: 1;
  margin-left: 3em;
  margin-right: 1.19em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__close{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    height: 1.75em;
    display: flex;
    align-items: center;
    padding: 0 1em;
    position: relative;
    @include lastM(0 1.81em 0 0);
    span{
      font-size: 1em;
      line-height: 125%;
      position: relative;
      z-index: 2;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%) skewX(-20deg);
      height: 100%;
      width: calc(100% + 1em);
      background-color: $primary;
      border-radius: .25em 1em .25em 1em;
      transition: inherit;
      opacity: 0;
    }
    &:hover,
    &._active{
      color: #fff;
      &:before{
        opacity: 1;
      }
    }
  }
}

.nav-contacts{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 1.56em 0 0);
  }
}

.email-link{
  color: $primary;
  &:hover{
    color: $black;
  }
}

.burger{
  display: none;
}

.lines{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  display: flex;
  justify-content: space-between;
  padding: 0 14.5em;
  z-index: -1;
}

._line{
  height: 100%;
  width: 1px;
  background-color: #E9E9E9;
}


@media screen and (max-width: 992px){
  .lines{
    display: none;
  }

  .header{
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
    .menu{
      overflow-y: auto;
      height: $vh;
      width: 100%;
      z-index: 30;
      position: fixed;
      top: 0;
      left: 0;
      background-color: #fff;
      padding: 1.25em 1.25em 2.06em;
      justify-content: flex-end;
      transition: all .4s;
      transform: translateX(-120%);
    }
    .logo{
      &__icon{
        width: 2.5em;
        margin-right: .44em;
      }
      &__text{
        width: 9em;
      }
    }
    .nav{
      margin-bottom: auto;
    }
    .nav-contacts{
      &__item{
        @include lastM(0 0 1.25em);
      }
    }
  }

  .footer{
    padding: 1.25em 0 2.19em;
    .container{
      padding: 0;
    }
    &__inner{
      flex-direction: column;
    }
    .call-btn{
      display: none;
    }
    .logo{
      margin-bottom: 1.56em;
    }
  }

  .menu,
  .nav,
  .nav-contacts{
    flex-direction: column;
  }

  .menu{
    margin: 0;
    &__close{
      @include center;
      width: 1.875em;
      margin: 0 0 1.2em auto;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }

  .nav{
    &__link{
      @include lastM(0 0 1.25em);
    }
  }

  .nav-contacts{
    margin-top: 1.875em;
    text-align: center;
    margin-bottom: 1.25em;
    &__item{
      @include lastM(0 0 .625em);
    }
  }

  .email-link{
    display: flex;
  }

  .call-btn{
    padding: 0;
    border: none;
    .text16{
      display: none;
    }
    &__rect{
      margin: 0;
      width: 4em;
      height: 3.5625em;
    }
  }

  .burger{
    @include center;
    width: 1.875em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}
