.projects2{
  .projects__top{
    max-width: 77em;
    .text30{
      line-height: 130%;
    }
  }
}

.events{
  padding: 5.19em 0 2em;
  background: #F5F5F5;
  &__top{
    max-width: 36em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 1.5em;
    margin-top: 5em;
  }
}

._hover-img{
  overflow: hidden;
  img{
    transition: all .4s;
  }
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
}

.event-card{
  display: block;
  &:hover{
    color: $black;
    .event-card__content{
      color: $primary;
    }
  }
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(80%);
  }
  &__content{
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    padding: .875em 1.75em;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 53.17%, rgba(0, 0, 0, 0.80) 100%);
    font-family: $font2;
    color: #fff;
    transition: all .3s;
  }
  &__bottom{
    padding: 0 1.75em;
    margin-top: 1.19em;
  }
  &__text{
    @include truncate(3);
    font-weight: 400;
  }
  &__arrow{
    margin-top: 1.5em;
    width: 5.4375em;
    display: block;
  }
}

.help{
  padding-top: 2em;
  background: #F5F5F5;
  .container{
    padding-right: 0;
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 36.375em) 36.375em;
  }
  &__content{
    padding: 3.44em 2em 2em 0;
  }
  &__top{
    @include grid100gap(.69em);
    max-width: 39em;
  }
  &__items{
    display: flex;
    justify-content: space-between;
    margin-top: 6.31em;
  }
  .service-item{
    @media screen and (min-width: 993px){
      &:nth-child(3n - 2){
        transform: translateY(-3.25em);
      }
      &:nth-child(3n - 1){
        transform: translateY(-.94em);
      }
    }
  }
}

.help-form{
  background-color: $black;
  height: 100%;
  color: #fff;
  padding: 3.75em 3.69em 11.5em;
  position: relative;
  &__btn{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 9.5625em;
    width: 100%;
    padding: 0 3em 0 0;
    img{
      width: 6.25em;
    }
  }
  .req__fields{
    grid-template-columns: 100%;
    gap: .81em;
    margin-top: 2.56em;
  }
}

.contacts2{
  @media screen and (min-width: 993px){
    padding-top: 0;
  }
}

.team{
  padding: 3.19em 0 4.37em;
  background: #F5F5F5;
  &__grid{
    margin-top: 3.69em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.19em));
    gap: 3em 4.38em;
  }
}

.team-card{
  &__badge{
    height: 2.25em;
    display: inline-flex;
    align-items: center;
    padding: 0 1.25em;
    background-color: $primary;
    color: #fff;
    span{
      font-size: 1.125em;
    }
  }
  &__wrap{
    display: grid;
    align-items: flex-start;
    grid-template-columns: 11.5em calc(100% - 13.5em);
    gap: 2em;
    background-color: #fff;
  }
  &__img{
    display: flex;
    align-items: flex-end;
    justify-content: center;
    height: 10em;
    padding: 0 1.25em;
    border-bottom: .3125em solid $primary;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    padding: 1em 1em 1em 0;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-bottom: .625em;
    border-bottom: 1px dashed #B7B7B7;
    .text14{
      font-weight: 400;
      color: #999;
    }
  }
  &__info{
    @include grid100gap(.375em);
  }
  &__date{
    margin-left: .5em;
    flex-shrink: 0;
    white-space: nowrap;
  }
  &__text{
    margin-top: 1.12em;
    display: flex;
    align-items: flex-start;
    font-style: italic;
    &:before{
      content: '';
      flex-shrink: 0;
      background-image: url(../img/quote.svg);
      width: 1.58119rem;
      height: 1.25rem;
      @include bgImgSize(100%);
      margin-right: 1.25em;
    }
  }
}

.about-main{
  padding: 0 0 3.19em;
  position: relative;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 41em) 39.0625em;
    justify-content: space-between;
    padding-right: 2em;
  }
  &__content{
    padding-top: 7.81em;
  }
  &__logo{
    position: absolute;
    left: 35.5em;
    top: 9.37em;
    width: 16.75em;
    z-index: -1;
    animation-name: rotation;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
  &__bg{
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
    width: 65.625em;
  }
  &__text{
    @include grid100gap(1.875em);
    max-width: 27.25em;
  }
  &__num{
    margin-left: -1em;
    display: flex;
    font-family: $font2;
    &-info{
      margin-left: .25em;
    }
    &-plus{
      font-size: 5.625em;
    }
    &-year{
      font-size: 4.5em;
      margin-top: -.375em;
    }
  }
  &__images{
    img{
      margin-top: -2em;
      width: 100%;
      display: block;
    }
  }
}

.advantages2{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3em / 4));
  gap: 1em;
  margin-top: -5.5em;
}

.advantage-item2{
  padding: .75em 1.75em 1.25em;
  &:hover{
    box-shadow:0 0 2.875em rgba($primary,.3);
    .advantage-item__img{
      transform: scale(1.05);
    }
  }
  .advantage-item__desc{
    max-width: 15em;
  }
  .advantage-item__img{
    height: 7.5em;
    top: unset;
    transform: unset;
    bottom: 2.25em;
  }
  &__subtitle{
    font-family: $font2;
    //margin-bottom: -.5em;
  }
  &__subtitle2{
    font-family: $font2;
    margin-top: -1.25em;
  }
  &__desc{
    margin-top: -.44em;
  }
}


@media screen and (max-width: 992px){
  .events{
    padding: 2em 0 1em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      margin-top: 1em;
    }
  }

  .help{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 1.25em;
    }
    &__items{
      margin-top: 1em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .team{
    padding: 2em 0;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .about-main{
    padding-bottom: 2em;
    &__bg,
    &__images,
    &__logo{
      display: none;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__content{
      padding-top: 2em;
    }
    &__text{
      gap: 1em;
    }
    &__num{
      margin-left: -.25em;
      &-info{
        font-size: .45em;
      }
    }
  }

  .advantages2{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    margin-top: 1em;
  }

  .advantage-item2{
    display: block;
    .advantage-item__desc{
      padding: 0;
      margin: -1em 0 0;
    }
    .advantage-item__img{
      height: 4.375em;
      bottom: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .events{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .help{
    .title2{
      max-width: 90%;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .help-form{
    padding: 1.25em 1.25em 7.3125em;
    &__btn{
      height: 5.3125em;
      padding: 0 3em;
      img{
        width: 3.75em;
      }
    }
    .req__fields{
      margin-top: 1.25em;
    }
  }

  .team-card{
    &__wrap{
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__img{
      padding-top: 1em;
    }
    &__content{
      padding: 0 1em 1em;
    }
    &__top{
      display: block;
    }
    &__info{
      gap: .125em;
    }
    &__date{
      margin: .25em 0 0;
    }
    &__text{
      margin-top: .625em;
      &:before{
        width: 1em;
        background-position: center top;
        margin-right: .25em;
      }
    }
  }

  .advantages2{
    grid-template-columns: 100%;
  }

  .advantage-item2{
    padding: .75em;
  }
}
